









export default {
	data() {
		return {
			title:'',
			node:'',
			
		};
	},
	onLoad:function(e){
		console.log(e)
		uni.showLoading({
			title:'加载中....'
		})
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newid,
			method: 'GET',
			data: {},
			success: res => {
				
				uni.hideLoading();
				this.title = res.data.title;
				this.node = res.data.content;
				console.log(res)
				
			},
			fail: () => {},
			complete: () => {}
		});
	}
}



